/** 断言是否是 PromiseLike */export function isPromiseLike(x) {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  return typeof (x === null || x === void 0 ? void 0 : x.then) === "function";
} /** 延迟 */
export function delay(ms = 0) {
  return new Promise(resolve => {
    setTimeout(resolve, ms);
  });
}
export function defer() {
  let status = "pending";
  let value;
  let reason;
  let resolve;
  let reject;
  const promise = new Promise((resolve_, reject_) => {
    resolve = resolve_;
    reject = reject_;
  }).then(value_ => {
    status = "fulfilled";
    value = value_;
    return value;
  }, reason_ => {
    status = "rejected";
    reason = reason_;
    throw reason;
  });
  return {
    get status() {
      return status;
    },
    get value() {
      return value;
    },
    get reason() {
      return reason;
    },
    get resolve() {
      return resolve;
    },
    get reject() {
      return reject;
    },
    get promise() {
      return promise;
    }
  };
}