import { assign } from "@pkg/utils";
import { Modal as Component } from "./modal";
import { state } from "./state"; /**
                                 * 显示模态框
                                 */
const open = (render, options) => {
  const id = state.add({
    render,
    options
  });
  const closeIt = () => close(id);
  return closeIt;
}; /**
   * 隐藏模态框
   */
const close = id => {
  state.delete(id);
}; /**
   * 模态框组件
   */
export const Modal = assign(Component, {
  open,
  close,
  clear: state.clear
});