import { assign } from "@pkg/utils";
import { Confirm as Component } from "./confirm";
import { state } from "./state"; /**
                                 * 显示模态框
                                 */
const open = item => {
  return state.add(item);
}; /**
   * 隐藏模态框
   */
const close = id => {
  state.delete(id);
}; /**
   * 模态框组件
   */
export const Confirm = assign(Component, {
  open,
  close,
  clear: state.clear
});