import { useCallback, useRef } from "react"; /**
                                             * 丐版 useEffectEvent
                                             *
                                             * @see https://react.dev/learn/separating-events-from-effects#declaring-an-effect-event
                                             */
export function useEffectEvent(fn) {
  const ref = useRef(fn);
  ref.current = fn;
  return useCallback((...args) => {
    return ref.current(...args);
  }, []);
}