import { Collector } from "byted-tea-sdk";
import { OPTIONS } from "./const";
export const tea = new Collector("default");
export function init() {
  tea.init(OPTIONS);
  tea.config({
    // 公共属性
    app_version: __BUILD_VERSION__,
    evtParams: {
      env: __ENV__,
      version: __BUILD_VERSION__
    }
  });
  tea.start();
} /**
  * 获取 utm 参数 并删除 url utm 的内容
  */
export function getPVParams() {
  const {
    pathname,
    host,
    search
  } = location;
  const referrer = `${host}${pathname}${search}`;
  const query = new URLSearchParams(location.search);
  const utm = {
    referrer,
    utm_source: query.get("utm_source") || undefined,
    utm_medium: query.get("utm_medium") || undefined,
    utm_term: query.get("utm_term") || undefined,
    utm_content: query.get("utm_content") || undefined,
    utm_campaign: query.get("utm_campaign") || undefined
  }; /// 兼容逻辑 start
  const $source = query.get("source"); // 以前的 source
  if (!$source) return utm; /// 兼容逻辑以 https://bytedance.feishu.cn/sheets/DgP0sYktThMbdGtstzRcGYQinrQ?sheet=5d04b1 文档为准
  const SEM_BAIDU_PREFIX = "baidu-sem-";
  const SEM_360_PREFIX = "360";
  if ($source.startsWith(SEM_BAIDU_PREFIX)) {
    // 百度 cpc
    Object.assign(utm, {
      utm_source: "baidu",
      utm_medium: "cpc",
      utm_term: $source.slice(SEM_BAIDU_PREFIX.length)
    });
  } else if ($source.startsWith(SEM_360_PREFIX)) {
    // 360 cpc
    Object.assign(utm, {
      utm_source: "360",
      utm_medium: "cpc",
      utm_term: $source.slice(SEM_360_PREFIX.length)
    });
  } else {
    // 兼容老逻辑
    Object.assign(utm, OLD_UTM_MAP[$source]);
  } /// 兼容逻辑 end
  return utm;
}
